import React, { useState } from "react"

import { DMTextInput } from "@supernovaio/dm"

type TextProps = {
  defaultValue?: string
  onChange: (value: string) => void
}

export function Text({ defaultValue, onChange }: TextProps) {
  const [value, setValue] = useState(defaultValue)

  return (
    <DMTextInput
      value={value}
      onChange={(e) => {
        const { value: newValue } = e.target

        setValue(newValue)
        onChange(newValue)
      }}
    />
  )
}
