import React from "react"
import { createRoot } from "react-dom/client"

import { DMProvider } from "@supernovaio/dm"

import { Playground } from "./components/playground"

import "@supernovaio/dm/styles.css"

const DATA_EMBED_URL = "data-storybook-embed-url"
const DATA_ORIGINAL_URL = "data-storybook-original-url"
const DATA_HEIGHT = "data-storybook-height"
const DATA_SHOW_FOOTER = "data-storybook-show-footer"
const DATA_SHOW_SOURCE_CODE = "data-storybook-show-source-code"
const DATA_SHOW_PROPERTIES = "data-storybook-show-properties"
const DATA_SHOW_DESCRIPTION = "data-storybook-show-description"
const DATA_SHOW_DEFAULTS = "data-storybook-show-defaults"

const elements = document.querySelectorAll(`[${DATA_EMBED_URL}]`)

elements.forEach((element) => {
  const url = element.getAttribute(DATA_EMBED_URL)
  const originalUrl = element.getAttribute(DATA_ORIGINAL_URL) ?? undefined

  const parsedHeight = parseInt(element.getAttribute(DATA_HEIGHT) ?? "", 10)
  const height = Number.isNaN(parsedHeight) ? 400 : parsedHeight

  const showFooter = element.getAttribute(DATA_SHOW_FOOTER) !== null
  const showSourceCode = element.getAttribute(DATA_SHOW_SOURCE_CODE) !== null
  const showProperties = element.getAttribute(DATA_SHOW_PROPERTIES) !== null
  const showDescription = element.getAttribute(DATA_SHOW_DESCRIPTION) !== null
  const showDefaults = element.getAttribute(DATA_SHOW_DEFAULTS) !== null

  if (!url) {
    return
  }

  const root = createRoot(element)
  root.render(
    <DMProvider>
      <Playground
        embedUrl={url}
        originalUrl={originalUrl}
        height={height}
        shouldShowFooter={showFooter}
        shouldShowSourceCode={showSourceCode}
        shouldShowProperties={showProperties}
        shouldShowDescription={showDescription}
        shouldShowDefaults={showDefaults}
      />
    </DMProvider>
  )
})
