import {
  ArgType,
  ArgValues,
  BooleanControl,
  ControlType,
  EventData,
  EventDataKey,
  ObjectControl,
  RadioControl,
  SelectControl,
  StorybookEvent,
  StorybookEventStoryPrepared,
  TextControl,
  Value,
  Parameters,
  StorybookEventSnippetRendered,
} from "./types"

export function isStorybookEventStoryPrepared(
  event: EventData["event"]
): event is StorybookEventStoryPrepared {
  return event.type === StorybookEvent.StoryPrepared
}

export function isStorybookEventSnippetRendered(
  event: EventData["event"]
): event is StorybookEventSnippetRendered {
  return event.type === StorybookEvent.SnippetRendered
}

export function isBooleanControl(argType: ArgType): argType is BooleanControl {
  return argType.control?.type === ControlType.Boolean
}

export function isTextControl(argType: ArgType): argType is TextControl {
  return argType.control?.type === ControlType.Text
}

export function isSelectControl(argType: ArgType): argType is SelectControl {
  return argType.control?.type === ControlType.Select
}

export function isRadioControl(argType: ArgType): argType is RadioControl {
  return argType.control?.type === ControlType.Radio
}

export function isObjectControl(argType: ArgType): argType is ObjectControl {
  return argType.control?.type === ControlType.Object
}

export function isDisplayedControl(
  argType: ArgType
): argType is Exclude<ArgType, { control: undefined }> {
  return (
    isBooleanControl(argType) ||
    isTextControl(argType) ||
    isSelectControl(argType) ||
    isRadioControl(argType) ||
    isObjectControl(argType)
  )
}

export function isBooleanDefaultValue(
  defaultValue: Value
): defaultValue is boolean | undefined {
  return typeof defaultValue === "boolean" || defaultValue === undefined
}

export function isStringDefaultValue(
  defaultValue: Value
): defaultValue is string | undefined {
  return typeof defaultValue === "string" || defaultValue === undefined
}

export function isObjectDefaultValue(
  defaultValue: Value
): defaultValue is object | undefined {
  return typeof defaultValue === "object" || defaultValue === undefined
}

export function isGroupedControl(
  argType: [string, ArgType]
): argType is [string, Required<Exclude<ArgType, { control: undefined }>>] {
  return !!argType[1].control && !!argType[1].table?.category
}

export function getEventData(event: MessageEvent) {
  return (
    typeof event.data === "string" ? JSON.parse(event.data) : event.data
  ) as EventData
}

export function getStorySource(parameters?: Parameters) {
  return parameters?.docs?.source?.originalSource || ""
}

export function updateControl(
  storyId: string,
  updatedArgs: ArgValues,
  window?: Window | null
) {
  if (!window) {
    return
  }

  window.postMessage(
    JSON.stringify({
      key: EventDataKey.Storybook,
      event: {
        type: StorybookEvent.UpdateStoryArgs,
        args: [
          {
            storyId,
            updatedArgs,
          },
        ],
      },
    }),
    "*"
  )
}

export function resetControls(storyId: string, window?: Window | null) {
  if (!window) {
    return
  }

  window.postMessage(
    JSON.stringify({
      key: EventDataKey.Storybook,
      event: { type: StorybookEvent.ResetStoryArgs, args: [{ storyId }] },
    }),
    "*"
  )
}
