export enum EventDataKey {
  Storybook = "storybook-channel",
}

export enum StorybookEvent {
  StoryPrepared = "storyPrepared",
  UpdateStoryArgs = "updateStoryArgs",
  ResetStoryArgs = "resetStoryArgs",
  SnippetRendered = "storybook/docs/snippet-rendered",
}

export enum ControlType {
  Boolean = "boolean",
  Text = "text",
  Select = "select",
  Radio = "radio",
  Object = "object",
}

type Table = {
  category: string
  readonly: boolean
}

type ControlBase = {
  name: string
  description?: string
  table?: Table
}

export type BooleanControl = ControlBase & {
  control: { type: ControlType.Boolean }
}

export type TextControl = ControlBase & {
  control: { type: ControlType.Text }
}

export type SelectControl = ControlBase & {
  control: { type: ControlType.Select }
  options: string[]
}

export type RadioControl = ControlBase & {
  control: { type: ControlType.Radio }
  options: string[]
}

export type ObjectControl = ControlBase & {
  control: { type: ControlType.Object }
}

export type ArgType =
  | BooleanControl
  | TextControl
  | SelectControl
  | RadioControl
  | ObjectControl
  | {
      control: undefined
    }

export type ArgTypes = Record<string, ArgType>

export type Value = boolean | string | object | undefined
export type ArgValues = Record<string, Value>

export type EventData = {
  key: string
  event: {
    type: string
  }
}

export type Parameters = {
  docs?: {
    source?: {
      originalSource?: string
    }
  }
}

export type StoryArgs = {
  id: string
  argTypes: ArgTypes
  initialArgs: ArgValues
  parameters?: Parameters
}

export type StorybookEventStoryPrepared = {
  type: "storyPrepared"
  args: StoryArgs[]
}

export type StorybookEventSnippetRendered = {
  type: "storybook/docs/snippet-rendered"
  args: {
    id: string
    source?: string
  }[]
}
