import React, { useState } from "react"

import { DMSelect, DMSelectItem } from "@supernovaio/dm"

type SelectProps = {
  defaultValue?: string
  options: string[]
  onChange: (value: string) => void
}

export function Select({ defaultValue, options, onChange }: SelectProps) {
  const [value, setValue] = useState(defaultValue)

  return (
    <DMSelect
      value={value}
      onValueChange={(newValue) => {
        setValue(newValue)
        onChange(newValue)
      }}
    >
      {options.map((option) => (
        <DMSelectItem key={option} value={option}>
          {option}
        </DMSelectItem>
      ))}
    </DMSelect>
  )
}
