import React, { useState } from "react"

import { DMAlert, DMButtonWhite } from "@supernovaio/dm"

import { IconCheck, IconCopy, IconInfo } from "@supernovaio/icons"

import { CodeSyntaxHighlighter } from "./CodeSyntaxHighlighter"

const COPY_TIMEOUT = 1000

type CodeTabProps = {
  sourceCode?: string
}

export function CodeTab({ sourceCode }: CodeTabProps) {
  const [hasBeenRecentlyCopied, setHasBeenRecentlyCopied] = useState(false)

  return sourceCode ? (
    <div className="relative">
      <CodeSyntaxHighlighter language="jsx">{sourceCode}</CodeSyntaxHighlighter>

      <DMButtonWhite
        className="absolute top-16 right-16"
        icon={
          hasBeenRecentlyCopied ? (
            <IconCheck width={16} height={16} />
          ) : (
            <IconCopy width={16} height={16} />
          )
        }
        size="small"
        onClick={async () => {
          await navigator.clipboard.writeText(sourceCode)
          setHasBeenRecentlyCopied(true)

          setTimeout(() => {
            setHasBeenRecentlyCopied(false)
          }, COPY_TIMEOUT)
        }}
      />
    </div>
  ) : (
    <div className="px-12 pb-12">
      <DMAlert color="neutral">
        <div className="flex items-center gap-8">
          <IconInfo width={16} height={16} />
          No source code available for this story
        </div>
      </DMAlert>
    </div>
  )
}
