import React, { useRef, useState } from "react"

import {
  DMLink,
  DMTabItem,
  DMTabList,
  DMTabPanel,
  DMTabs,
} from "@supernovaio/dm"

import { IconStorybookColored } from "@supernovaio/icons"

import { useStory } from "../../hooks/useStory"
import { resetControls } from "../../utils"
import { CodeTab } from "../code/CodeTab"
import { Controls } from "../controls/Controls"
import { WithResizableWrapper } from "../ui/EditableHeightWrapper"

import { v4 as uuidv4 } from "uuid"

import "@supernovaio/dm/styles.css"

enum Tabs {
  PROPERTIES = "properties",
  CODE = "code",
}

type PlaygroundProps = {
  embedUrl: string
  originalUrl?: string
  allowResize?: boolean
  minHeight?: number
  maxHeight?: number
  height: number
  shouldShowFooter?: boolean
  shouldShowSourceCode?: boolean
  shouldShowProperties?: boolean
  shouldShowDescription?: boolean
  shouldShowDefaults?: boolean
  onHeightChange?: (newHeight: number) => void
}

export function Playground({
  embedUrl,
  originalUrl,
  allowResize,
  minHeight = 100,
  maxHeight = 2000,
  height,
  shouldShowFooter,
  shouldShowSourceCode,
  shouldShowProperties,
  shouldShowDescription,
  shouldShowDefaults,
  onHeightChange,
}: PlaygroundProps) {
  const storyId = new URL(embedUrl).searchParams.get("id")

  const { storyArgs, storySourceCode } = useStory(storyId)

  const storyRef = useRef<HTMLIFrameElement>(null)

  // this is temporary, as we will be moving state to block data in the future
  const [stateId, setStateId] = useState<string | null>(() => uuidv4())

  const handleReset = () => {
    setStateId(uuidv4())
    if (!storyId) {
      return
    }

    resetControls(storyId, storyRef.current?.contentWindow)
  }

  return (
    <div className="flex flex-col">
      <WithResizableWrapper
        allowResize={allowResize}
        minHeight={minHeight}
        maxHeight={maxHeight}
        height={height}
        onHeightChange={(newHeight) => {
          onHeightChange?.(newHeight)
        }}
      >
        <iframe
          height={height}
          className="w-full"
          ref={storyRef}
          title="Playground"
          src={embedUrl}
        />
      </WithResizableWrapper>
      {shouldShowSourceCode && shouldShowProperties && storyArgs && storyId && (
        <DMTabs>
          <div className="p-12">
            <DMTabList variant="pills" size="small">
              <DMTabItem id={Tabs.PROPERTIES}>Properties</DMTabItem>
              <DMTabItem id={Tabs.CODE}>Code</DMTabItem>
            </DMTabList>
          </div>

          <DMTabPanel tabId={Tabs.PROPERTIES}>
            <Controls
              key={stateId}
              storyArgs={storyArgs}
              storyId={storyId}
              storyRef={storyRef}
              shouldShowDescription={shouldShowDescription}
              shouldShowDefaults={shouldShowDefaults}
              onReset={handleReset}
            />
          </DMTabPanel>

          <DMTabPanel tabId={Tabs.CODE}>
            <CodeTab sourceCode={storySourceCode} />
          </DMTabPanel>
        </DMTabs>
      )}

      {shouldShowProperties &&
        !shouldShowSourceCode &&
        storyArgs &&
        storyId && (
          <Controls
            key={stateId}
            storyArgs={storyArgs}
            storyId={storyId}
            storyRef={storyRef}
            shouldShowDescription={shouldShowDescription}
            shouldShowDefaults={shouldShowDefaults}
            onReset={handleReset}
          />
        )}

      {shouldShowSourceCode &&
        !shouldShowProperties &&
        storyArgs &&
        storyId && <CodeTab sourceCode={storySourceCode} />}

      {shouldShowFooter && originalUrl && storyArgs && storyId && (
        <div className="p-12 border-t border-neutral-faded">
          <DMLink
            href={originalUrl}
            isExternalLink
            innerClassName="flex items-center gap-4"
          >
            <IconStorybookColored width={16} height={16} />
            Open in Storybook
          </DMLink>
        </div>
      )}
    </div>
  )
}
