import React, { useState } from "react"

import { DMTableBody, DMTableCell, DMTableRow } from "@supernovaio/dm"

import { IconCaret, IconCaretOpen } from "@supernovaio/icons"

import { ArgType, ArgValues } from "../../types"
import { isDisplayedControl } from "../../utils"

import { Control } from "./Control"

type ControlTableGroupContentProps = {
  initialArgs: ArgValues
  argTypes: [string, ArgType][]
  storyId: string
  storyRef: React.RefObject<HTMLIFrameElement>
  shouldShowDescription?: boolean
  shouldShowDefaults?: boolean
}

export function ControlTableGroupContent({
  initialArgs,
  argTypes,
  storyId,
  storyRef,
  shouldShowDescription,
  shouldShowDefaults,
}: ControlTableGroupContentProps) {
  return argTypes.map(([key, argType]) => {
    if (!isDisplayedControl(argType)) {
      return null
    }

    const defaultValue = initialArgs[key]

    return (
      <DMTableRow key={key}>
        <DMTableCell className="font-semibold">{argType.name}</DMTableCell>
        {shouldShowDescription && (
          <DMTableCell>{argType.description}</DMTableCell>
        )}
        {shouldShowDefaults && (
          <DMTableCell>
            {typeof defaultValue === "object" ? "-" : defaultValue}
          </DMTableCell>
        )}
        <DMTableCell>
          <Control
            argType={argType}
            defaultValue={defaultValue}
            storyId={storyId}
            storyRef={storyRef}
          />
        </DMTableCell>
      </DMTableRow>
    )
  })
}

type ControlTableGroupProps = ControlTableGroupContentProps & {
  name: string
}

export function ControlTableGroup({
  name,
  initialArgs,
  argTypes,
  storyId,
  storyRef,
  shouldShowDescription,
  shouldShowDefaults,
}: ControlTableGroupProps) {
  const [isExpanded, setIsExpanded] = useState(false)

  return (
    <DMTableBody className="text-body-small font-normal">
      <DMTableRow onClick={() => setIsExpanded(!isExpanded)}>
        <DMTableCell
          colSpan={4}
          className="font-semibold cursor-pointer bg-neutral text-neutral-faded"
        >
          <div className="flex items-center gap-2">
            {isExpanded ? <IconCaretOpen /> : <IconCaret />}
            {name}
          </div>
        </DMTableCell>
      </DMTableRow>
      {isExpanded && (
        <ControlTableGroupContent
          initialArgs={initialArgs}
          argTypes={argTypes}
          storyId={storyId}
          storyRef={storyRef}
          shouldShowDescription={shouldShowDescription}
          shouldShowDefaults={shouldShowDefaults}
        />
      )}
    </DMTableBody>
  )
}
