import React, { useState } from "react"

import { JsonControl } from "../../lib/controls/Json/Json"

type JsonProps = {
  defaultValue?: object
  onChange: (value: object) => void
}

export function Json({ defaultValue, onChange }: JsonProps) {
  const [value, setValue] = useState(defaultValue ?? {})

  return (
    <JsonControl
      name="json"
      value={value}
      onChange={(newValue) => {
        setValue(newValue ?? {})
        onChange(newValue ?? {})
      }}
    />
  )
}
