import React from "react"

import {
  DMButtonNeutral,
  DMTable,
  DMTableBody,
  DMTableHead,
  DMTableHeader,
  DMTableRow,
  DMTooltip,
} from "@supernovaio/dm"

import { IconReset } from "@supernovaio/icons"

import { StoryArgs } from "../../types"
import { isGroupedControl } from "../../utils"

import {
  ControlTableGroup,
  ControlTableGroupContent,
} from "./ControlTableGroup"

type ControlsProps = {
  storyArgs: StoryArgs
  storyId: string
  storyRef: React.RefObject<HTMLIFrameElement>
  shouldShowDescription?: boolean
  shouldShowDefaults?: boolean
  onReset?: () => void
}

export function Controls({
  storyArgs,
  storyId,
  storyRef,
  shouldShowDescription,
  shouldShowDefaults,
  onReset,
}: ControlsProps) {
  const { argTypes, initialArgs } = storyArgs

  const argTypesArray = Object.entries(argTypes)

  const ungroupedArgTypes = argTypesArray.filter(
    ([, argType]) => argType.control && !argType.table?.category
  )

  const groupedArgTypes = Object.groupBy(
    argTypesArray.filter(isGroupedControl),
    ([, argType]) => {
      return argType.table.category
    }
  )

  return (
    <DMTable className="table-fixed w-full">
      <DMTableHeader>
        <DMTableRow>
          <DMTableHead className="bg-neutral">Name</DMTableHead>
          {shouldShowDescription && (
            <DMTableHead className="bg-neutral">Description</DMTableHead>
          )}
          {shouldShowDefaults && (
            <DMTableHead className="bg-neutral">Default</DMTableHead>
          )}
          <DMTableHead className="bg-neutral">
            <div className="flex items-center gap-4 justify-between">
              Control
              <DMTooltip text="Reset controls">
                <DMButtonNeutral
                  size="small"
                  variant="ghost"
                  icon={<IconReset width={16} height={16} />}
                  onClick={onReset}
                />
              </DMTooltip>
            </div>
          </DMTableHead>
        </DMTableRow>
      </DMTableHeader>
      {ungroupedArgTypes.length > 0 && (
        <DMTableBody>
          <ControlTableGroupContent
            initialArgs={initialArgs}
            argTypes={ungroupedArgTypes}
            storyId={storyId}
            storyRef={storyRef}
            shouldShowDescription={shouldShowDescription}
            shouldShowDefaults={shouldShowDefaults}
          />
        </DMTableBody>
      )}
      {Object.entries(groupedArgTypes).map(([category, args]) => {
        return (
          <ControlTableGroup
            key={category}
            name={category}
            initialArgs={initialArgs}
            argTypes={args ?? []}
            storyId={storyId}
            storyRef={storyRef}
            shouldShowDescription={shouldShowDescription}
            shouldShowDefaults={shouldShowDefaults}
          />
        )
      })}
    </DMTable>
  )
}
