import React, { useEffect, useRef, useState } from "react"

import { cn } from "@supernovaio/dm/src/utils/cn"

/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */

export type EditableHeightWrapperProps = {
  height: number
  onHeightChange: (height: number) => void
  onDragStop?: ((height: number) => Promise<void>) | ((number: number) => void)
  onDragStart?: (() => Promise<void>) | (() => void)
  onClick?: () => void
  children: React.ReactNode
  className?: string
  style?: React.HTMLAttributes<HTMLDivElement>["style"]
  minHeight: number
  maxHeight: number
  heightLabel?: string
  isDisabled?: boolean
}

export function EditableHeightWrapper({
  height,
  onHeightChange,
  onDragStop,
  onDragStart,
  onClick,
  children,
  className,
  style,
  heightLabel,
  minHeight,
  maxHeight,
  isDisabled,
}: EditableHeightWrapperProps) {
  const dirty = useRef(false)
  const wrapper = useRef<HTMLDivElement | null>(null)
  const [dragging, setDragging] = useState(false)

  useEffect(() => {
    if (isDisabled) {
      return
    }
    if (dragging) {
      dirty.current = true
      // eslint-disable-next-line @typescript-eslint/no-floating-promises
      onDragStart?.()
    }
    if (!dragging && dirty.current) {
      // eslint-disable-next-line @typescript-eslint/no-floating-promises
      onDragStop?.(height)
      dirty.current = false
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dragging])
  useEffect(() => {
    const { y: wrapperY } = wrapper.current?.getBoundingClientRect() || {
      y: 0,
      height: 0,
    }

    function handleMouseUp() {
      if (dragging) {
        setDragging(false)
      }
    }

    function handleMouseMove(this: Window, ev: WindowEventMap["mousemove"]) {
      const newMinHeight = ev.clientY - wrapperY

      if (dragging && newMinHeight !== height) {
        onHeightChange(
          // floor to avoid floating point precision issues
          Math.floor(Math.min(Math.max(newMinHeight, minHeight), maxHeight))
        )
      }
    }

    // Add the window mouseup and mousemove listeners when the component mounts or dragging state changes
    window.addEventListener("mouseup", handleMouseUp)
    window.addEventListener("mousemove", handleMouseMove)

    // Clean up the listeners when the component unmounts or dragging state changes
    return () => {
      window.removeEventListener("mouseup", handleMouseUp)
      window.removeEventListener("mousemove", handleMouseMove)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dragging, height, minHeight])

  return (
    <div
      ref={wrapper}
      className={cn(
        "group/editable-height-wrapper relative z-10 border-b border-neutral-faded",
        className
      )}
      style={{
        ...style,
        minHeight: height,
      }}
      onClick={onClick}
    >
      {children}
      {!isDisabled && (
        <div
          className="invisible -translate-x-1/2 group-hover/editable-height-wrapper:visible flex flex-col items-center w-[140px] absolute left-1/2 bottom-0"
          style={{
            zIndex: dragging ? 1001 : 10,
          }}
        >
          {heightLabel && dragging && (
            <div className="text-body-small rounded-small max-w-[140px] select-none overflow-hidden border-none bg-black px-8 py-4 font-semibold text-white mb-4">
              {heightLabel}
            </div>
          )}
          <div
            className={cn(
              "hover:bg-primary",
              "rounded-full h-[4px] w-[32px] transform box-content translate-y-[3px]",
              dragging ? "bg-primary" : "bg-border-neutral scale-105"
            )}
            style={{
              cursor: dragging ? "grabbing" : "ns-resize",
            }}
            onMouseDown={(e) => {
              // skip when not pressing left mouse button
              if (e.button !== 0) {
                return
              }
              e.preventDefault()
              setDragging(true)
            }}
          />
        </div>
      )}
      {dragging && (
        <div
          style={{
            position: "fixed",
            cursor: "grabbing",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            zIndex: 1000,
          }}
        />
      )}
    </div>
  )
}

type WithResizableWrapperProps = {
  allowResize?: boolean
  children: React.ReactNode
} & EditableHeightWrapperProps

export function WithResizableWrapper({
  allowResize,
  children,
  ...rest
}: WithResizableWrapperProps) {
  if (!allowResize) {
    return <div>{children}</div>
  }

  return <EditableHeightWrapper {...rest}>{children}</EditableHeightWrapper>
}
