import React from "react"

import { cn } from "../../../utils/cn"
import type { ActionableRef } from "../../Actionable/Actionable.types"
import { Button } from "../../primitives/Button"
import { DMButtonBaseProps } from "../../primitives/Button/Button.types"
import { VariantClasses } from "../DMButton.types"

type ButtonWhiteProps = Omit<DMButtonBaseProps, "variant"> & {
  noShadow?: boolean
}

function DMButtonWhiteBase(props: ButtonWhiteProps, ref: ActionableRef) {
  const {
    size = "medium",
    isFullWidth = false,
    isLoading = false,
    isDisabled = false,
    isHighlighted = false,
    noShadow = false,
    children,
    ...rest
  } = props

  const variantClasses: VariantClasses = {
    solid: {
      base: cn("bg-elevation-raised text-neutral border !border-neutral-faded", {"shadow-raised": !noShadow,}), // "!border-neutral-faded" Wee should probably add new button variant – Raised (force overriding of non-border condition for other variants than outline)
      hover: "hover:text-primary hover:!border-neutral",
      focus: "focus-visible:ring-2",
      highlighted: "text-primary",
    },
  }

  return (
    <Button
      {...rest}
      ref={ref}
      isDisabled={isDisabled}
      isFullWidth={isFullWidth}
      isHighlighted={isHighlighted}
      isLoading={isLoading}
      size={size}
      variant="solid"
      variantStyles={variantClasses.solid}
    >
      {children || ""}
    </Button>
  )
}

const DMButtonWhite = React.forwardRef(
  DMButtonWhiteBase
) as React.ForwardRefExoticComponent<ButtonWhiteProps>

DMButtonWhite.displayName = "DMButtonWhite"

export { DMButtonWhite }
