import React from "react"

import { ArgType, Value } from "../../types"
import {
  isBooleanControl,
  isBooleanDefaultValue,
  updateControl,
  isTextControl,
  isStringDefaultValue,
  isSelectControl,
  isRadioControl,
  isObjectControl,
  isObjectDefaultValue,
} from "../../utils"

import { Boolean } from "./Boolean"
import { Json } from "./Json"
import { Radio } from "./Radio"
import { Select } from "./Select"
import { Text } from "./Text"

type ControlProps = {
  argType: ArgType
  defaultValue: Value | undefined
  storyId: string
  storyRef: React.RefObject<HTMLIFrameElement>
}

export function Control({
  argType,
  defaultValue,
  storyId,
  storyRef,
}: ControlProps) {
  if (isBooleanControl(argType) && isBooleanDefaultValue(defaultValue)) {
    return (
      <Boolean
        id={argType.name}
        defaultValue={defaultValue}
        onChange={(value) => {
          updateControl(
            storyId,
            {
              [argType.name]: value,
            },
            storyRef.current?.contentWindow
          )
        }}
      />
    )
  }

  if (isTextControl(argType) && isStringDefaultValue(defaultValue)) {
    return (
      <Text
        defaultValue={defaultValue}
        onChange={(value) => {
          updateControl(
            storyId,
            { [argType.name]: value },
            storyRef.current?.contentWindow
          )
        }}
      />
    )
  }

  if (isSelectControl(argType) && isStringDefaultValue(defaultValue)) {
    return (
      <Select
        defaultValue={defaultValue}
        onChange={(value) => {
          updateControl(
            storyId,
            { [argType.name]: value },
            storyRef.current?.contentWindow
          )
        }}
        options={argType.options}
      />
    )
  }

  if (isRadioControl(argType) && isStringDefaultValue(defaultValue)) {
    return (
      <Radio
        defaultValue={defaultValue}
        onChange={(value) => {
          updateControl(
            storyId,
            { [argType.name]: value },
            storyRef.current?.contentWindow
          )
        }}
        options={argType.options}
      />
    )
  }

  if (isObjectControl(argType) && isObjectDefaultValue(defaultValue)) {
    return (
      <Json
        defaultValue={defaultValue}
        onChange={(value) => {
          updateControl(
            storyId,
            { [argType.name]: value },
            storyRef.current?.contentWindow
          )
        }}
      />
    )
  }

  return null
}
