import type { ComponentProps, SyntheticEvent } from "react"
import React from "react"

import { DMButtonNeutral, DMButtonPrimary, DMTextInput } from "@supernovaio/dm"

import { IconClose, IconMinus, IconPlus, IconSave } from "@supernovaio/icons"

import { JsonTree } from "../react-editable-json-tree"

import "./json.css"

const { window: globalWindow } = globalThis

type ControlProps<T> = {
  name: string
  value?: T
  onChange: (value?: T) => T | void
}

export type ObjectValue = object

type JsonTreeProps = ComponentProps<typeof JsonTree>

const ENTER_EVENT = {
  bubbles: true,
  cancelable: true,
  key: "Enter",
  code: "Enter",
  keyCode: 13,
}

const dispatchEnterKey = (event: SyntheticEvent<HTMLInputElement>) => {
  event.currentTarget.dispatchEvent(
    new globalWindow.KeyboardEvent("keydown", ENTER_EVENT)
  )
}

const selectValue = (event: SyntheticEvent<HTMLInputElement>) => {
  event.currentTarget.select()
}

export type ObjectProps = ControlProps<ObjectValue>

const getCustomStyleFunction: () => JsonTreeProps["getStyle"] = () => () => ({
  ul: {
    listStyle: "none",
    margin: "0 0 0 1rem",
    padding: 0,
  },
  li: {
    outline: 0,
  },
})

export function JsonControl({ name, value, onChange }: ObjectProps) {
  return (
    <div className="relative flex">
      <JsonTree
        data={value}
        rootName={name}
        onFullyUpdate={onChange}
        getStyle={getCustomStyleFunction()}
        cancelButtonElement={
          <DMButtonNeutral type="button" size="small" icon={<IconClose />} />
        }
        editButtonElement={
          <DMButtonNeutral type="submit" size="small" icon={<IconSave />} />
        }
        addButtonElement={
          <DMButtonPrimary type="submit" size="small" icon={<IconSave />} />
        }
        plusMenuElement={
          <IconPlus className="cursor-pointer" width={12} height={12} />
        }
        minusMenuElement={
          <IconMinus className="cursor-pointer" width={12} height={12} />
        }
        // eslint-disable-next-line react/no-unstable-nested-components
        inputElement={(_, __, ___, key: string) =>
          key ? (
            <DMTextInput
              size="small"
              onFocus={selectValue}
              onBlur={dispatchEnterKey}
            />
          ) : (
            <DMTextInput size="small" />
          )
        }
      />
    </div>
  )
}
