import { useEffect, useState } from "react"

import { EventDataKey, StoryArgs } from "../types"
import {
  getEventData,
  isStorybookEventSnippetRendered,
  isStorybookEventStoryPrepared,
  getStorySource,
} from "../utils"

export function useStory(storyId?: string | null) {
  const [storyArgs, setStoryArgs] = useState<StoryArgs | null>(null)
  const [storySourceCode, setStorySourceCode] = useState<string>()

  useEffect(() => {
    const onMessage = (messageEvent: MessageEvent) => {
      const data = getEventData(messageEvent)

      if (data.key !== EventDataKey.Storybook) {
        return
      }

      const { event } = data

      if (isStorybookEventSnippetRendered(event)) {
        const currentStoryArgs = event.args.find((arg) => arg.id === storyId)

        if (!currentStoryArgs) {
          return
        }

        setStorySourceCode(currentStoryArgs?.source)
        return
      }

      if (!isStorybookEventStoryPrepared(event)) {
        return
      }

      const currentStoryArgs = event.args.find((arg) => arg.id === storyId)

      if (!currentStoryArgs) {
        return
      }

      setStoryArgs(currentStoryArgs)
      setStorySourceCode(getStorySource(currentStoryArgs.parameters))
    }

    window.addEventListener("message", onMessage)

    return () => {
      window.removeEventListener("message", onMessage)
    }
  }, [storyId])

  return { storyArgs, storySourceCode }
}
