import React, { useState } from "react"

import { DMSwitch } from "@supernovaio/dm"

type BooleanProps = {
  id: string
  defaultValue?: boolean
  onChange: (value: boolean) => void
}

export function Boolean({ id, defaultValue, onChange }: BooleanProps) {
  const [value, setValue] = useState(defaultValue)

  return (
    <div className="flex">
      <DMSwitch
        id={id}
        isChecked={value}
        onChange={() => {
          setValue((prev) => {
            const newValue = !prev

            onChange(newValue)

            return newValue
          })
        }}
      />
    </div>
  )
}
