import React from "react"

import type { ActionableRef } from "../../Actionable/Actionable.types"
import { Button } from "../../primitives/Button"
import { DMButtonBaseProps } from "../../primitives/Button/Button.types"

function DMButtonNeutralBase(props: DMButtonBaseProps, ref: ActionableRef) {
  const {
    variant = "solid",
    size = "medium",
    isLoading = false,
    isDisabled = false,
    isHighlighted = false,
    children,
    ...rest
  } = props

  return (
    <Button
      ref={ref}
      isDisabled={isDisabled}
      isHighlighted={isHighlighted}
      isLoading={isLoading}
      size={size}
      variant={variant}
      {...rest}
    >
      {children || ""}
    </Button>
  )
}

const DMButtonNeutral = React.forwardRef(
  DMButtonNeutralBase
) as React.ForwardRefExoticComponent<DMButtonBaseProps>

export { DMButtonNeutral }
