import React, { useState } from "react"

import { DMRadioGroup, DMRadioGroupItem } from "@supernovaio/dm"

type RadioProps = {
  defaultValue?: string
  options: string[]
  onChange: (value: string) => void
}

export function Radio({ defaultValue, options, onChange }: RadioProps) {
  const [value, setValue] = useState(defaultValue)

  return (
    <DMRadioGroup
      value={value}
      onValueChange={(newValue) => {
        setValue(newValue)
        onChange(newValue)
      }}
    >
      {options.map((option) => (
        <DMRadioGroupItem key={option} label={option} value={option} />
      ))}
    </DMRadioGroup>
  )
}
